.home-container {
    height: calc(100vh - 60px);
    margin: 30px;
    background-color: var(--light-blue);
    position: relative;
    padding: 30px;
}

.home-container h1 {
    color: var(--blue);
}

.down {
    color: black;
    position: absolute;
    z-index: 99;
    font-size: 3rem;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.home-content {
    display: flex;
    flex-wrap:wrap;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

#profile-pic {
    border-radius: 50%;
    height: 300px;
    box-shadow: 10px 10px 0 var(--blue);
    border: 3px solid var(--dark-blue);
}

.home-text h1 {
    font-size: 3rem;
}